.card-container{
    background-color: blue;
    color: white;
    width: 200px;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 7px;
    align-items: center;
    padding: 10px;
    margin: 10px;
    cursor: pointer;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
    flex-grow: 1;
}
.card-container:hover{
    transform: scale(1.05);
}